import { Layout } from "../../components/layout";
import React from "react";
import { Image, Carousel } from "antd";
import { useParams } from "react-router-dom";

const seriesCount = {
  1: 85,
  2: 114,
  3: 74,
  4: 199,
  5: 26,
  6: 75,
};

const portfolioCount = {
  1: 8,
  2: 5,
  3: 9,
  4: 8,
  5: 5,
  6: 8,
}

const horizonPics = {
  1: [
    10, 14, 19, 20, 22, 25, 29, 31, 33, 34, 35, 36, 40, 45, 49, 50, 51, 56, 58,
    60, 64, 65, 72, 74,
  ],
  2: [9, 12, 24, 25, 34, 48, 48, 68, 85, 87, 110],
  3: [10, 23, 25, 35, 37, 41, 49, 60],
  4: [7, 49, 65, 87, 126, 136, 158],
};

export const Detail = (props) => {
  // 从路由中获取参数
  const { id } = useParams();
  const count = seriesCount[id];
  const images = [];
  const portfolioImages = [];
  const mainVisualIDs = horizonPics[id] || [];
  for (let i = 1; i <= count; i++) {
    if (mainVisualIDs.includes(i)) continue;
    try{
      images.push(
        <Image
          src={require(`../../static/series${id}/more/thumb/${i}.jpg`)}
          width={180}
          preview={{
            src: require(`../../static/series${id}/more/${i}.jpg`),
          }}
        />
      );
    }catch(e){
      console.error(e);
      continue;
    }
  }

  for(let i = 1; i <= portfolioCount[id]; i++){
    try{
      portfolioImages.push(
        <Image
          src={require(`../../static/series${id}/portfolio/${i}.jpg`)}
          // width={180}
          preview={false}
        />
      );
    }catch(e){
      console.error(e);
      continue;
    }
  }

  return (
    <Layout>
      <div style={{ minWidth: 1600 }}>
        <div style={{ width: 1500, margin: "auto" }}>
          <Carousel arrows autoplay>
            {mainVisualIDs.map((mainVisualID) => (
              <img
                src={require(`../../static/series${id}/more/${mainVisualID}.jpg`)}
                alt=""
              ></img>
            ))}
          </Carousel>
        </div>
        <div style={{
          color:'#666',
          fontSize: 48,
          marginTop: 36,
          marginBottom: 24,
          marginLeft: 36,
          fontFamily:'"Raleway",sans-serif,"google"'
        }}>GALLERY</div>
        <div style={{ paddingLeft: 36 }}>
          <Image.PreviewGroup>{images}</Image.PreviewGroup>
        </div>
        <div style={{
          color:'#666',
          fontSize: 48,
          marginTop: 36,
          marginBottom: 24,
          marginLeft: 36,
          fontFamily:'"Raleway",sans-serif,"google"'
        }}>PORTFOLIO</div>
        <div style={{ paddingLeft: 36 }}>
          {portfolioImages}
        </div>
      </div>
    </Layout>
  );
};

export default Detail;
