import React, { useContext, useEffect, useState } from "react";
import { ConfigContext } from "../../context";
import "./index.scss";
import { InstagramOutlined, TikTokOutlined } from "@ant-design/icons";
import { Menu, ConfigProvider } from "antd";
import { useNavigate } from "react-router-dom";

export const Layout = ({ children }) => {
  const [selectedKeys, setSelectedKeys] = useState([]);
  const config = useContext(ConfigContext);
  const navigate = useNavigate();
  useEffect(() => {
    //路由hash中是否含有detail
    if (window.location.hash.includes("list")) {
      setSelectedKeys(["portfolio"]);
    } else {
      setSelectedKeys([]);
    }
  }, []);
  const navigation = [
    {
      label: "PORTFOLIO",
      key: "portfolio",
    },
    {
      label: "BIO",
      key: "bio",
    },
    {
      label: "EXHIBITION",
      key: "exhibition",
    },
  ];
  return (
    <div className="layout">
      <div className="header">
        <div className="name">
          <span className="name-content" onClick={() => navigate("/list")}>
            {config.name}
          </span>
          <span className="icon">
            <InstagramOutlined onClick={()=>{
              window.open(config.instgramURL)
            }} />
            <TikTokOutlined onClick={()=>{
              window.open(config.tiktokURL)
            }} style={{ marginLeft: "4px" }} />
          </span>
        </div>
        <div className="menu">
          <ConfigProvider
            theme={{
              components: {
                Menu: {
                  horizontalItemSelectedColor: "#000",
                },
              },
            }}
          >
            <Menu
              mode="horizontal"
              items={navigation}
              style={{ fontSize: "18px" }}
              selectedKeys={selectedKeys}
              onSelect={({ key }) => {
                if (key === "portfolio") {
                  navigate("/list");
                }
              }}
            />
          </ConfigProvider>
        </div>
      </div>
      {children}
    </div>
  );
};
